import React from "react";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import DefaultNav from "../../DefaultNav/DefaultNav";
import { apiGet, apiPost } from "../../api";

let nombre = "";
var cantidad = 1;

var lista_recetas = {};
let lista_nombre_recetas_display = [];

export default function Registry(props) {
  let fecha = new Date().toLocaleDateString("en-CA");
  let history = useHistory();
  let location = useLocation();
  const [recipess, setRecipes] = React.useState([]);
  const [nombree, setnombre] = React.useState("");
  const [uid, setuid] = React.useState("");
  const [cantidadd, setcantidad] = React.useState("");
  const [lista_ingredientess, setlista] = React.useState({});
  const [fechaa, setfecha] = React.useState("");

  function parse_recipies(recipies_list) {
    let lista_salida = [];
    for (let i = 0; i < recipies_list.length; i++) {
      const element = recipies_list[i];
      lista_salida.push(element.name);
    }

    return lista_salida;
  }

  function get_uid_recipe(recipies_list, name) {
    for (let i = 0; i < recipies_list.length; i++) {
      const element = recipies_list[i];
      if (element.name == name) {
        return element.uid;
      }
    }
  }

  const handleSubmitForm = (event) => {
    event.preventDefault();

    let body = {
      date: fechaa,
      recipies_uids_add: {
        [uid]: {
          quantity: cantidad
        }
      }
    };
    var xhr = new XMLHttpRequest();
    xhr.open(
      "POST",
      "https://manarea.xyz/area/" + location.state.id + "/add_registry",
      true
    );
    xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
    xhr.setRequestHeader("accept", "application/json");

    xhr.send(
      JSON.stringify({
        date: fechaa,
        recipies_uids_add: {
          [uid]: {
            quantity: cantidad
          }
        }
      })
    );
    //body["recipies_uids_add"][[uid]] = { "quantity": cantidad };
    console.log("body", body);
    //apiPost(`area/${location.state.id}/add_registry`, body);
    //window.location.reload(false);
    //history.push("/ZoneDashboard", { id: props.id_zona });
    console.log("los pits", props);
  };
  useEffect(() => {
    getRecipes();
  }, []);

  async function getRecipes() {
    //console.log(location);

    const recipes_data = await apiGet(
      "area/" + location.state.id + "/get_all_recipies"
    );

    //console.log(recipes_data);
    setRecipes(await recipes_data);
    setfecha(fecha);
    setnombre(recipes_data[0]["name"]);
    setuid(get_uid_recipe(recipes_data, recipes_data[0]["name"]));

    return await recipes_data;
  }

  const ClickIngredient = (event) => {
    //console.log("Clickeado");

    listaChange(event);
  };

  const listaChange = (event) => {
    var values = event.target.value.split(",");
    console.log("values", event.target.value);
    let nombre_receta = values[0];

    lista_recetas[nombre_receta] = { cantidad: values[1], uid: uid };
    console.log(lista_recetas);
    setlista(event.target.value);
  };

  const nameChange = (event) => {
    setnombre(event.target.value);
    nombre = event.target.value;
    console.log(nombre);
    setuid(get_uid_recipe(recipess, nombre));
  };

  const DeleteIngredient = (event) => {
    //lista_ingredientes.removeItem(event.target.value);
    //console.log("lista ingredientes", lista_recetas);
    //console.log("llave", event.target.value);
    delete lista_recetas[event.target.value];
    //console.log("lista ingredientes", lista_recetas);
    //lista_ingredientes = Object.omit(lista_ingredientes, event.target.value);
    setnombre(event.target.value);
  };

  const cantidadChange = (event) => {
    setcantidad(event.target.value);
    cantidad = event.target.value;
    //console.log(cantidad);
  };

  const fechaChange = (event) => {
    setfecha(event.target.value);
    fecha = event.target.value;
    lista_recetas = {};
    lista_nombre_recetas_display = [];
  };

  lista_nombre_recetas_display = parse_recipies(recipess);

  return (
    <div>
      <DefaultNav />
      <form onSubmit={handleSubmitForm}>
        <section class="section">
          <div class="columns is-mobile">
            <div class="column is-one-quarter ">
              <label class="label">Fecha</label>
              <input
                class="input "
                type="date"
                placeholder={fecha}
                defaultValue={fecha}
                required
                onChange={fechaChange}
              />
            </div>
          </div>

          <div class="columns is-mobile">
            <div class="column is-one-quarter ">
              <label class="label">Receta</label>
              <div class="select is-fullwidth ">
                <select
                  required
                  name="nombre"
                  value={nombree}
                  onChange={nameChange}
                >
                  {lista_nombre_recetas_display.map((row) => (
                    <option>{row}</option>
                  ))}
                </select>
              </div>
            </div>
            <div class="column is-one-quarter ">
              <label class="label">Cantidad</label>
              <input
                class="input "
                type="number"
                min="1"
                max="199"
                placeholder="1"
                defaultValue="1"
                onChange={cantidadChange}
                required
              />
            </div>
            <div class="column">
              <label class="label">Agregar</label>
              <button
                class="button is-link"
                value={[nombree, cantidad, fechaa]}
                onClick={ClickIngredient}
                type="button"
              >
                +
              </button>
            </div>
          </div>

          <h1 class="title ">Recetas</h1>
          <h2 class="subtitle">
            <ul>
              {Object.keys(lista_recetas).map((row) => (
                <div class="columns is-mobile">
                  <div class="column is-two-quarter">
                    <li>
                      {row} - {lista_recetas[row].cantidad}
                    </li>
                  </div>

                  <div class="column">
                    <button
                      class="button is-link"
                      value={row}
                      onClick={DeleteIngredient}
                      type="button"
                    >
                      x
                    </button>
                  </div>
                </div>
              ))}
            </ul>
          </h2>
        </section>
        <section class="section">
          <button class=" button is-primary ">Submit</button>
          <section />
        </section>
      </form>
    </div>
  );
}
