import React from "react";
import Nav from "./Nav";
import { Route, Switch, BrowserRouter, Redirect } from "react-router-dom";
import SignIn from "./Components/SignIn/SignIn";
import Dashboard from "./Components/Dashboard/Dashboard";
import DefaultLanding from "./Components/Dashboard/DefaultLanding";
import ZoneDashboard from "./Components/Dashboard/Zones/ZoneDashboard";
import Recipes from "./Components/Dashboard/Zones/Recipes/Recipes";
import Registry from "./Components/Dashboard/Zones/Registry";
import History from "./Components/Dashboard/Zones/History";
import EditRecipe from "./Components/Dashboard/Zones/Recipes/EditRecipe";

const Home = () => <h1 className="title">Home</h1>;
const Documentation = () => <h1 className="title">Documentation</h1>;
const About = () => <h1 className="title">About</h1>;
const Jobs = () => <h1 className="title">Jobs</h1>;
const Contact = () => <h1 className="title">Contact</h1>;

export default function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/about">
          <Nav />
        </Route>
        <Route path="/SignIn">
          <SignIn />
        </Route>
        <Route exact path="/">
          <Redirect to="/Dashboard" />
        </Route>
        <Route path="/Dashboard">
          <Dashboard />
        </Route>
        <Route path="/DefaultLanding">
          <DefaultLanding />
        </Route>
        <Route path="/ZoneDashboard">
          <ZoneDashboard />
        </Route>
        <Route path="/Recipes">
          <Recipes />
        </Route>
        <Route path="/Registry">
          <Registry />
        </Route>
        <Route path="/History">
          <History />
        </Route>
        <Route path="/EditRecipe">
          <EditRecipe />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}
