import React from "react";
import { useState, useEffect } from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import DefaultNav from "../../DefaultNav/DefaultNav";
import "react-calendar/dist/Calendar.css";
import Calendar from "react-calendar";
import "./Calendar.scss";
import { apiGet } from "../../api";
import HistoryCard from "./HistoryCard";
import moment from "moment";

let test = {
  id: 1,
  calendar: {
    "2022-01-01": {
      recipies_uids: {
        "03d788f1-8aea-448d-87a2-93c4c850d370": 5,
        "9246f6f6-017e-4b11-96ce-89b2e2e71432": 5
      }
    },
    "2022-01-05": {
      recipies_uids: {
        "03d788f1-8aea-448d-87a2-93c4c850d370": 1,
        "9246f6f6-017e-4b11-96ce-89b2e2e71432": 1
      }
    },
    "2022-01-13": {
      recipies_uids: {
        "03d788f1-8aea-448d-87a2-93c4c850d370": 3,
        "9246f6f6-017e-4b11-96ce-89b2e2e71432": 1
      }
    }
  }
};
let mark = [];
//console.log(Object.keys(test));

var minDate = 0;
var maxDate = 0;
let lista_dias = [];
let nombresRecetass = [{}];
let resumen_general = {
  recetas: {},
  resumen_insumos: {}
};
export default function History() {
  const [calDate, setCalDate] = useState(new Date());
  const [nombresRecetas, setNombresRecetas] = useState([]);
  const location = useLocation();
  useEffect(() => {
    get_registries();
    setNombresRecetas(...nombresRecetas);
  }, []);

  //const [bloqueo, setBloqueo] = useState(1);
  function get_dates(container_list) {
    return Object.keys(container_list);
  }

  function expenses_recipes(container_list) {
    let final_array_recipies = resumen_general["recetas"];
    let final_array_supplies = resumen_general["resumen_insumos"];

    //console.log("container_list", container_list);
    container_list.forEach((element) => {
      if (Object.keys(final_array_recipies).includes(element.name) == false) {
        final_array_recipies[element.name] = element.quantity;
      } else {
        final_array_recipies[element.name] += element.quantity;
      }
      //console.log(
      //"element.ingredientselement.ingredients",
      //element.ingredients
      //);
      Object.keys(element.ingredients).map((entrada) => {
        if (Object.keys(final_array_supplies).includes(entrada) == false) {
          final_array_supplies[entrada] =
            element.ingredients[entrada].quantity *
            element.ingredients[entrada].weight;
        } else {
          final_array_supplies[entrada] +=
            element.ingredients[entrada].quantity *
            element.ingredients[entrada].weight;
        }
      });
    });
    console.log("resumen_general", resumen_general);
    setNombresRecetas(...[nombresRecetas]);
  }

  async function get_registries() {
    let registries = await apiGet(
      `area/${location.state.id}/get_all_registries`
    );
    test = registries;
    mark = get_dates(test);
    setNombresRecetas(...[nombresRecetas]);
  }

  async function get_recipe_by_uid(recipe_uid, quantity) {
    let contenedor = await apiGet(
      `area/${location.state.id}/get_recipe/${recipe_uid}`
    );
    //console.log("contenedor", contenedor);
    contenedor["quantity"] = quantity;
    nombresRecetass.push(contenedor);
    setNombresRecetas(...[contenedor]);
    if (contenedor.message == "Recipe not found") {
      console.log("Receta no encontrada");
    } else {
      expenses_recipes([contenedor]);
    }

    contenedor["quantity"] = quantity;
    nombresRecetass.push(contenedor);
    setNombresRecetas(...[contenedor]);
    //nombresRecetass.push(contenedor);

    return contenedor;
  }

  function onChange(calDate) {
    // console.log(calDate);
    // change results based on calendar date click
    //if (bloqueo == 1) {
    setCalDate(calDate);
    minDate = calDate[0].toLocaleDateString("en-CA");
    maxDate = calDate[1].toLocaleDateString("en-CA");
    lista_dias = [];
    nombresRecetass = [];
    resumen_general = {
      recetas: {},
      resumen_insumos: {}
    };

    let loop = new Date(minDate);
    while (loop <= new Date(maxDate)) {
      //console.log(loop);
      lista_dias.push(loop.toISOString("en-CA").slice(0, 10));
      let newDate = loop.setDate(loop.getDate() + 1);
      loop = new Date(newDate);
      //}
      //setBloqueo(0);
      /*} else {
      setBloqueo(1);
    }*/

      //console.log('dias',lista_dias)
    }
    lista_dias.forEach((row) => {
      if (Object.keys(test).includes(row) === true)
        Object.keys(test[row]["recipies_uids"]).forEach((llave) => {
          //console.log(
          //"ASDASDASDASD",
          //test[row]["recipies_uids"][llave]
          //);
          get_recipe_by_uid(llave, test[row]["recipies_uids"][llave]);
        });
    });
    //console.log("nombresRecetas", nombresRecetas);
    //console.log("nombresRecetasssss", nombresRecetass);
  }

  // NO OLVIDAR
  //const { handle } = useParams();
  //const location = useLocation();
  //console.log(location.state);

  //console.log(minDate);
  //console.log(maxDate);
  return (
    <div>
      <DefaultNav />

      <div>
        <section class="section ">
          <div class="columns is-centered is-mobile">
            <div className="result-calendar ">
              <Calendar
                onChange={onChange}
                value={calDate}
                selectRange={true}
                markingType={"multi-dot"}
                tileContent={({ date, view }) => {
                  if (
                    mark.find((x) => x === moment(date).format("YYYY-MM-DD"))
                  ) {
                    return (
                      <svg height="14.5" width="7">
                        <circle
                          cx="3.5"
                          cy="3.7"
                          r="3"
                          stroke="black"
                          stroke-width="1"
                          fill="#13c6e7"
                        />
                      </svg>
                    );
                  }
                }}
              />
            </div>
          </div>
          {Object.keys(resumen_general["recetas"]).length == 0 ? null : (
            <div class="columns is-mobile">
              <div class="column is-two-quarter">
                <ul>
                  <div>
                    <HistoryCard {...resumen_general} />
                  </div>
                </ul>
              </div>
            </div>
          )}
        </section>
      </div>
    </div>
  );
}
