import React from "react";
import { RouterLink, Route, Link } from "react-router-dom";

export default function ZoneCard(props) {
  const id = props.id;
  const name = props.name;
  const description = props.description;

  return (
    <div>
      <section className="section">
        <div class="card">
          <header class="card-header">
            <p class="card-header-title">
              {id} - {name}
            </p>
          </header>
          <div class="card-content">
            <div class="content">
              {description}
              <br />
              <br />s Usuarios autorizados para editar registro:
              <ul>
                <li>Sra. Vivi</li>
                <li>Maguita</li>
              </ul>
              <br />
              <p>Ultima Actualizacion de Registro</p>
              <time datetime="2016-1-1">11:09 PM - 1 Jan 2016</time>
            </div>
          </div>
          <footer class="card-footer">
            <Link
              class="card-footer-item"
              to={{ pathname: "/Recipes", state: { id: id } }}
            >
              Recetas
            </Link>
            <Link
              class="card-footer-item"
              to={{ pathname: "/Registry", state: { id: id } }}
            >
              Registro
            </Link>
            <Link
              class="card-footer-item"
              to={{ pathname: "/History", state: { id: id } }}
            >
              Historial
            </Link>
          </footer>
        </div>
      </section>

      <span class="icon">
        <i class="fas fa-home"></i>
      </span>
    </div>
  );
}
