import React from "react";
import { RouterLink, Route, Link } from "react-router-dom";

export default function ZoneCard(props) {
  const recetas = props.recetas;
  const resumen_insumos = props.resumen_insumos;

  return (
    <div>
      <section className="section">
        <div class="card">
          <div class="card-content">
            <div class="content">
              <h1 class="title">Recetas</h1>
              <ul>
                {Object.keys(recetas).map((row) => (
                  <li>
                    Nombre Receta: {row} - Cantidad hecha: {recetas[row]}
                  </li>
                ))}
              </ul>
            </div>
            <div class="content">
              <h1 class="title">Resumen Insumos</h1>

              <ul>
                <div>
                  <div class="columns is-mobile">
                    <div class="column is-one-quarter ">
                      <label class="label">Nombre Ingrediente</label>
                    </div>
                    <div class="column is-three-quarter ">
                      <label class="label">Cantidad ocupada</label>
                    </div>
                  </div>
                </div>
                {Object.keys(resumen_insumos).map((row) => (
                  <div>
                    <div class="columns is-mobile">
                      <div class="column is-one-quarter ">{row}</div>
                      <div class="column is-three-quarter ">
                        {resumen_insumos[row]} KG
                      </div>
                    </div>
                  </div>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </section>

      <span class="icon">
        <i class="fas fa-home"></i>
      </span>
    </div>
  );
}
