import React from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { Link } from "react-router-dom";

export default function SingleRecipe(props) {
  let history = useHistory();
  //console.log(props,'props');

  function getIngredients(variables) {
    return Object.keys(variables["row"]["ingredients"]);
  }

  const DeleteRecipe = (event) => {
    //console.log(props.uid);
    var xhr = new XMLHttpRequest();
    xhr.open(
      "DELETE",
      "https://manarea.xyz/area/" +
        props.id_zona +
        "/remove_recipe/" +
        props.row.uid,
      true
    );
    xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");

    xhr.send(JSON.stringify({}));

    history.push("/Recipes", { id: props.id_zona });
  };
  //const { handle } = useParams();
  //const location = useLocation();
  //const { id } = 123; // location.state;
  //console.log(id);
  return (
    <div>
      <section className="section">
        <div class="card">
          <header class="card-header">
            <p class="card-header-title">
              {props.row.name} by: {props.row.author}{" "}
            </p>
          </header>
          <div class="card-content">
            <div class="content">
              <div class="columns is-mobile">
                <div class="column is-mobile ">
                  Ingredientes:
                  <ul>
                    {getIngredients(props).map((row) => (
                      <li>
                        {row} :{" "}
                        {props.row.ingredients[row].quantity *
                          props.row.ingredients[row].weight}{" "}
                        KG{" "}
                      </li>
                    ))}
                  </ul>
                  <br />
                  <p>Ultima Actualizacion de Registro</p>
                  <time datetime="2016-1-1">11:09 PM - 1 Jan 2016</time>
                </div>
                <div class="column is-mobile ">
                  <figure class="image is-256x256">
                    <img src="https://roggendorf.cl/wp-content/uploads/2016/12/Hallullas-min.jpg" />
                  </figure>
                </div>
              </div>
            </div>
          </div>
          <footer class="card-footer">
            <Link
              class="card-footer-item"
              to={{ pathname: "/EditRecipe", state: props }}
            >
              Editar
            </Link>

            <a onClick={DeleteRecipe} class="card-footer-item">
              Eliminar
            </a>
          </footer>
        </div>
      </section>

      <span class="icon">
        <i class="fas fa-home"></i>
      </span>
    </div>
  );
}
