import React from "react";
import { useState, useEffect } from "react";
import { RouterLink, useParams, useLocation } from "react-router-dom";
import { apiPost } from "../../api";
import { useHistory } from "react-router-dom";

export default function CreateZone() {
  let history = useHistory();
  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    // eslint-disable-next-line no-console

    var xhr = new XMLHttpRequest();
    xhr.open("POST", "https://manarea.xyz/area", true);
    xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
    xhr.send(
      JSON.stringify({
        name: data.get("nombre"),
        description: data.get("descripcion"),
        recipies: [],
        stored_goods: {}
      })
    );
    window.location.reload();
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <section class="section">
          <label class="label">Nombre de la Zona</label>
          <div class="control">
            <input
              required
              name="nombre"
              class="input"
              type="text"
              placeholder="Ej: Bodega"
            />
            <br />
            <br />
          </div>
          <div class="field">
            <label class="label">Descripcion de la Zona</label>
            <div class="control">
              <textarea
                required
                name="descripcion"
                class="textarea"
                placeholder="Ej: En esta zona donde se guardan los diferentes insumos..."
              ></textarea>
            </div>
          </div>

          <button class=" button is-primary" type="submit">
            Submit
          </button>
        </section>
      </form>
    </div>
  );
}
